.inputForm{
    background-color: goldenrod;
    box-shadow: 10px 10px 5px 0px rgba(61,87,60,0.75);
    margin-top: 10px;
    padding: 10px;
    border-radius: 10px;
}
.inputForm input{
    width: 300px;
    border: 1px solid white;
    outline: none;
    font-size: 15px;
    padding: 10px 10px;
    margin-bottom: 15px;
    background-color: white;
}
.inputForm textarea{
    width: 300px;
    border: 1px solid white;
    outline: none;
    font-size: 15px;
    padding: 10px 10px;
    margin-bottom: 15px;
    background-color: white;
}
.inputForm button{
    background-color: darkred;
    border: none;
    border-radius: 100px;
    padding: 10px;
    color: white;
    cursor: pointer;
    width:100px;
    margin-left: 30px;
}
